import './css/main.scss'
import { data, getHeaders, getFormData, getQueryParams } from './main.js'
import API from './api'
import setupEditors from './editor'

const urlForm = document.getElementById('urlForm')
const urlInput = urlForm.querySelector('[data-url]')
const selectedMethod = urlForm.querySelector('[data-method]')
const submitButton = urlForm.querySelector('[data-submit]')

// selectedMethod.querySelectorAll('option').forEach((optTag) => {
//     optTag.addEventListener('click', (ev) => {
//         selectedMethod.querySelector('[selected]')?.removeAttribute('selected')
//         ev.originalTarget.setAttribute('selected', true)
//     })
// })
const { requestEditor, updateResponseEditor } = setupEditors()

// selectedMethod.querySelector('option[value=get]').setAttribute('selected', true)

var responseTab = document.querySelector('[data-response-tabs]')

urlForm.addEventListener('submit', (ev) => {
    ev.preventDefault()
    data['url'] = urlInput.value.trim()
    data['method'] = selectedMethod.value.trim().toLowerCase()

    data['config'].headers = getHeaders()
    data['config'].queryParams = getQueryParams()

    const formData = getFormData(data['config'].headers)

    if (data['method'].toLowerCase() !== 'get') {
        try {
            data['body'] = JSON.parse(requestEditor.state.doc.toString())
        } catch (_) {
            data['body'] = {}
        }

        if (!Object.keys(data['body']).length) {
            data['body'] = formData
        }

        console.log(data['body'])
    }

    const startTime = new Date().getTime()

    const url = API.send(data, async (response) => {
        if (response) {
            updateResponseEditor(response.data)
            handleResponseDetails(response, startTime)
            handleResponseEditorState(response, updateResponseEditor)
        } else {
            const response = {
                data: 'Request timeout the remote server might be down',
                status: 408,
                statusText: 'Timeout',
            }

            handleResponseDetails(response, startTime)
            updateResponseEditor(response.data)
        }
    })

    if (url) {
        urlInput.value = url
    }
})

function handleResponseDetails(response, startTime) {
    if (!response) return

    const status = document.querySelector('#resStatusCode [data-inject]')
    status.innerHTML = `${response.status} ${response.statusText}`

    const duration = document.querySelector('#resDuration [data-inject]')
    duration.innerHTML = `${
        response.customData?.timeDuration || new Date().getTime() - startTime
    }ms`
}

function handleResponseEditorState(response, handler) {
    responseTab.childNodes.forEach((tab) => {
        tab.addEventListener('click', () => {
            responseTab.querySelector('.active').classList.remove('active')
            tab.classList.add('active')

            if (tab.hasAttribute('data-body-tab')) {
                handler(response.data)
            } else if (tab.hasAttribute('data-header-tab')) {
                handler(response.headers)
            }
        })
    })
}
