import axios from 'axios'

class BaseApiSetup {
    constructor() {
        this.axiosApi = axios
        this.config = {
            url: undefined,
            method: undefined,
            data: undefined,
            params: undefined,
            headers: undefined,
        }

        // this.init()
    }

    init() {
        this.axiosApi.interceptors.request.use((request) => {
            request.customData = request.duration || {}
            request.customData['startTime'] = new Date().getTime()
            return request
        })
        this.axiosApi.interceptors.response.use(
            (response) => {
                response.customData = response.customData || {}
                response.customData['timeDuration'] =
                    new Date().getTime() - response.config.customData.startTime
                return response
            },
            (error) => {
                return Promise.reject(error)
            },
        )
    }

    async act(callback) {
        let response = {}
        await this.axiosApi(this.config)
            .then((res) => {
                response = res
            })
            .catch((error) => {
                response = error.response
            })
        callback(response)
    }

    reset() {
        this.config = {
            url: undefined,
            method: undefined,
            data: undefined,
            params: undefined,
            headers: undefined,
        }
    }
}

class ApiRequests {
    constructor() {
        this.api = new BaseApiSetup()
        this.allowedMethod = ['get', 'post', 'put', 'get']
    }

    send(init = {}, callback) {
        const options = {
            url: '',
            method: 'get',
            body: null,
            config: { headers: {}, urlParams: [], queryParams: {} },
            ...init,
        }

        if (options.url.length < 10) return false
        if (!options.method in this.allowedMethod) return false
        if (options.method !== 'get' && !options.body) return false

        var href = this.setUrl(options.url)

        this.api.config = {
            url: href,
            method: options.method,
            params: options.config.queryParams,
            data: options.body,
            headers: options.config.headers,
        }

        this.perform(callback)
        return href + this.setQueryParameters(options.config.queryParams)
    }

    async perform(cb) {
        await this.api.act((response) => {
            cb(response)
        })
        this.api.reset()
    }

    setUrl(url) {
        if (url.split('?').length > 1) {
            url = url.split('?')[0]
        }
        return new URL(url.trim()).href
    }

    setQueryParameters(query) {
        var urlValue = ''

        if (Object.keys(query).length) {
            urlValue = '?'
            for (const key in query) {
                urlValue = urlValue + `${key.trim()}=${query[key]}&`
            }
        }
        urlValue = urlValue.split('')
        urlValue.pop()

        return urlValue.join('').trim()
    }
}
const API = new ApiRequests()
export default API
