import { EditorState, basicSetup } from '@codemirror/basic-setup'
import { defaultTabBinding } from '@codemirror/commands'
import { EditorView, keymap } from '@codemirror/view'
import { json } from '@codemirror/lang-json'

const baseTheme = EditorView.baseTheme({
    '.cm-o-replacement': {
        display: 'inline-block',
        width: '.5em',
        height: '.5em',
        borderRadius: '.25em',
    },
    '&light .cm-o-replacement': {
        backgroundColor: '#04c',
    },
    '&dark .cm-o-replacement': {
        backgroundColor: '#5bf',
    },
})

export default function setupEditors() {
    const jsonRequestBody = document.getElementById('requestEditor')
    const jsonResponseBody = document.getElementById('responseEditor')

    const basicExtensions = [
        basicSetup,
        keymap.of([defaultTabBinding]),
        EditorState.tabSize.of(4),
        baseTheme,
    ]

    const requestEditorHeight = EditorView.theme({
        '&': {
            minHeight: '100px',
            maxHeight: '150px',
            backgroundColor: '#fff',
            color: '#333',
        },
        '.cm-scroller': { overflow: 'auto' },
    })
    const requestEditor = new EditorView({
        state: EditorState.create({
            doc: '{\n\t\n}',
            extensions: [...basicExtensions, requestEditorHeight],
        }),
        parent: jsonRequestBody,
        viewport: { from: 0, to: 300 },
    })

    const responseEditorHeight = EditorView.theme({
        '&': {
            maxHeight: '350px',
            minHeight: '200px',
            color: '#333',
            backgroundColor: 'rgb(226 232 240)',
        },
        '.cm-scroller': { overflow: 'auto' },
    })
    const responseEditor = new EditorView({
        state: EditorState.create({
            doc: '...',
            extensions: [
                ...basicExtensions,
                EditorView.editable.of(false),
                responseEditorHeight,
            ],
        }),
        parent: jsonResponseBody,
    })

    function updateResponseEditor(value) {
        responseEditor.dispatch({
            changes: {
                from: 0,
                to: responseEditor.state.doc.length,
                insert: JSON.stringify(value, null, 2),
            },
        })
    }

    return { requestEditor, updateResponseEditor }
}
