var currentTab = document.querySelector('[data-tab].active')

const addNewFieldButtons = document.querySelectorAll(
    '[data-tab] .tab-content [data-add-field]',
)
const tabsButtons = document.querySelectorAll('[data-tab] .title')

const data = {
    url: '',
    method: '',
    config: { headers: {}, urlParams: [], queryParams: {} },
}

tabsButtons.forEach((tab) => {
    tab.addEventListener('click', () => {
        currentTab = tab
        tabsButtons.forEach((_tab) => {
            _tab.closest('[data-tab]').classList.remove('active')
        })
        currentTab.closest('[data-tab]').classList.add('active')
    })
})

addNewFieldButtons.forEach((button) => {
    button.addEventListener('click', () => {
        const template = document.querySelector('template[data-field')
        const keyValuePair = template.content.cloneNode(true)
        const content = button.closest('.tab-content').querySelector('.fields')
        content.appendChild(keyValuePair)
        content.scrollBy({ top: 100 })
        fieldRemoveEvent()
    })
})

function getHeaders() {
    const headers = {}
    const headerTab = document.getElementById('headerTab')
    headerTab
        .querySelectorAll('.tab-content .fields [data-key-value-pair]')
        .forEach((fields) => {
            if (
                fields.querySelector('[data-key]').value &&
                fields.querySelector('[data-value]').value
            ) {
                headers[fields.querySelector('[data-key]').value.trim()] =
                    fields.querySelector('[data-value]').value.trim()
            }
        })
    return headers
}

function getFormData(headers) {
    const form = new FormData()
    const json = {}
    const paramTab = document.getElementById('formData')
    paramTab
        .querySelectorAll('.tab-content .fields [data-key-value-pair]')
        .forEach((fields) => {
            if (
                fields.querySelector('[data-key]').value &&
                fields.querySelector('[data-key]').value
            ) {
                json[fields.querySelector('[data-key').value.trim()] = fields
                    .querySelector('[data-value')
                    .value.trim()
                form.append(
                    fields.querySelector('[data-key]').value.trim(),
                    fields.querySelector('[data-value]').value.trim(),
                )
            }
        })

    var value

    for (const key in headers) {
        if (headers[key].toLowerCase().includes('application/json')) {
            value = json
            break
        }
    }

    if (value) return json

    return form
}

function getQueryParams() {
    const params = {}
    const paramTab = document.getElementById('paramTab')
    paramTab
        .querySelectorAll('.tab-content .fields [data-key-value-pair]')
        .forEach((fields) => {
            if (
                fields.querySelector('[data-key]').value &&
                fields.querySelector('[data-key]').value
            ) {
                params[fields.querySelector('[data-key]').value.trim()] = fields
                    .querySelector('[data-value]')
                    .value.trim()
            }
        })
    return params
}

function fieldRemoveEvent() {
    const removeFieldButtons = document.querySelectorAll(
        '[data-tab] .tab-content [data-key-value-pair] span',
    )
    removeFieldButtons.forEach((removeBtn) => {
        removeBtn.addEventListener('click', () => {
            if (
                removeBtn
                    .closest('.tab-content')
                    ?.querySelectorAll('[data-key-value-pair]').length === 1
            ) {
                return
            }
            removeBtn.parentNode.parentNode.remove()
        })
    })
}
fieldRemoveEvent()

export { data, getHeaders, getFormData, getQueryParams }
